body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
  color: #333;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  text-align: center;
}

nav {
  margin-top: 10px;
}

nav a {
  margin: 0 15px;
  color: #61dafb;
  text-decoration: none;
}

nav a:hover {
  text-decoration: underline;
}

.about-section,
.projects-section,
.skills-section,
.contact-section {
  padding: 40px 20px;
  background-color: #fff;
  margin: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h2 {
  color: #333;
}

ul {
  list-style-type: none;
  padding: 0;
}

.project {
  margin-bottom: 20px;
}

.project h3 {
  margin-bottom: 5px;
}

.project p {
  margin: 0;
  color: #555;
}

.contact-section a {
  color: #61dafb;
  text-decoration: none;
}

.contact-section a:hover {
  text-decoration: underline;
}

.skills-section {
  padding: 20px;
  text-align: center;
}

.skills-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.skill-item {
  margin: 20px;
  text-align: center;
}

.skill-item img {
  width: 80px;
  height: 80px;
  margin-bottom: 10px;
}

.skill-item p {
  font-size: 1rem;
  color: #333;
}
